.header {
  background-color: #173e43;
  height: 100px;
  width: auto;
}
.header a {
  height: 100px;
  line-height: 100px;
  white-space: nowrap;
  padding-left: 50px;
  font-size: 48px;
  font-family: 'Open Sans', sans-serif;
  color: #FFFFFF;
  text-decoration-line: none;
  font-weight: bold;
}
.header .nav {
  float: right;
  list-style-type: none;
  margin-right: 50px;
}
.header .nav li {
  float: left;
  text-align: center;
}
.header .nav li a {
  display: inline-block;
  min-width: 100px;
  white-space: nowrap;
  padding-left: 12px;
  padding-right: 12px;
  font-size: 20px;
  font-family: 'Open Sans', sans-serif;
  color: #FFFFFF;
  text-decoration-line: none;
  font-weight: normal;
}
.header .nav li a:hover {
  background-color: #3fb0ac;
}
.active {
  background-color: #3fb0ac;
}
